@import "styles/variables";
@import "styles/mixins";

.list {
  @include resetList;

  display: flex;
  align-items: center;
}

.item {
  &:not(:first-child) {
    margin-left: 5rem;
  }
}

.link {
  @include fontNavigation;
  @include link;
}

.linkActive {
  color: $colorHighlight;
  text-decoration: underline;
}
