@import "styles/variables";

.header {
  position: absolute;
  top: 0;
  left: 0;
  padding-top: 4rem;
  padding-bottom: 4rem;
  background-color: $colorDarker;
  border-bottom: 0.1rem solid $colorBorderMain;
  width: 100%;
  z-index: 3;
  transform: translateY(0);
  transition-duration: $transitionDuration;
}

.headerHiddenAbsolute {
  position: absolute;
  transform: translateY(-100%);
}

.headerHiddenFixed {
  position: fixed;
  transform: translateY(-100%);
}

.headerFixed {
  position: fixed;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
  transform: translateY(0);
}

.wrapper {
  display: flex;
  align-items: center;
}

.navigation {
  margin-left: auto;
}

.button {
  margin-left: 4rem;
}
