@import "styles/variables";
@import "styles/mixins";

.footer {
  padding: 6rem 0 4.2rem 0;
  background-color: $colorDark;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.copyright {
  @include fontSmall(rgba(255, 255, 255, 0.25));

  margin: 3.3rem 0 0 0;
}

.link {
  @include fontSmall($colorFontLightTransparent);
  @include link;

  text-decoration: underline;
}
